@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer{
    background-color: v.$primario;
    .contenido-footer{
        padding: 5rem;

        @include m.tablet {
            @include m.grid(3,2rem);
        }
    }
    .copyright {
        color: v.$blanco;
        margin: 0;
        padding: 2rem;
        text-align: center;
    }
}

.footer-box{
    font-family: v.$fuenteCursiva;
    margin-bottom: v.$separacion;
    text-align: center;
    h3{
        color: v.$secundario;
        font-family: v.$fuenteCursiva;
        font-size: 2.4rem;
        margin: 0 0 1rem 0;
    }
    p , a{
        font-size: 2rem;
        margin: 0;
    }
}